import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import SearchContent from '../component/Search/SearchContent'

class Search extends Component {
  componentDidMount () {
    const {setDataLoaded, getComicList} = this.props

    setDataLoaded(false)
    getComicList()
  }

  render () {
    return (
      <main>
        <SearchContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    comicList: state.comicList,
    isDataLoaded: state.isDataLoaded
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getComicList: (query) => dispatch(actionCreators.getComicList(query)),
    updateComicItem: (comic) => dispatch(actionCreators.updateComicItem(comic)),
    toggleLocalFavorite: (comic) => dispatch(actionCreators.toggleLocalFavorite(comic)),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded)),
    setSelectComic: (selectComic) => dispatch(actionCreators.setSelectComic(selectComic))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Search)



