import React, {Component} from 'react'
import { withStyles } from '@material-ui/core/styles'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Toolbar from '@material-ui/core/Toolbar'
import TextField from '@material-ui/core/TextField'
import InputAdornment from '@material-ui/core/InputAdornment'

import FilterListIcon from '@material-ui/icons/FilterList'

import AppComicList from '../Common/AppComicList/AppComicList'

const styles = theme => ({
  root: {},
  loader: {
    margin: '5rem',
    textAlign: 'center'
  },
  filter: {
    padding: '.5rem 0',
    [theme.breakpoints.only('xs')]: {
      margin: '.5rem auto',
      width: 'calc(100% - 2rem)'
    }
  }
})

class SearchContent extends Component {
  constructor () {
    super()
    this.state = {
      filterValue: ''
    }
  }

  setFilterValue = (event) => this.setState({filterValue: event.target.value})

  onToggleFavorite = (event, comic) => {
    event.stopPropagation()

    const {
      updateComicItem,
      toggleLocalFavorite
    } = this.props

    comic.isFavorite = !comic.isFavorite

    updateComicItem(comic)
    toggleLocalFavorite(comic)
  }

  render () {
    const {
      classes,
      isDataLoaded,
      comicList,
      setSelectComic
    } = this.props
    const {filterValue} = this.state

    return (
      <div>
        <Grid container justify='center'>
          <Grid item xs={12}>
            <Toolbar className={classes.filter}>
              <TextField
                fullWidth
                value={this.state.filterValue}
                onChange={this.setFilterValue}
                margin='normal'
                placeholder='Which comic do you looking for ?'
                InputProps={{
                  startAdornment: (
                    <InputAdornment position='start'>
                      <FilterListIcon />
                    </InputAdornment>
                  )
                }} />
            </Toolbar>
          </Grid>
          <Grid item xs={12}>
            {
              !isDataLoaded &&
              <div className={classes.loader}>
                <CircularProgress size={80} />
              </div>
            }
            {
              isDataLoaded &&
              <AppComicList
                comicList={comicList.filter(comic => comic.title.indexOf(filterValue) > -1)}
                setSelectComic={setSelectComic}
                onToggleFavorite={this.onToggleFavorite} />
            }
          </Grid>
        </Grid>
      </div>
    )
  }
}

export default withStyles(styles)(SearchContent)



